import React from 'react';
import MainPage from "./MainPage";
import {HashRouter as Router, Route, Switch} from 'react-router-dom'
import FilterSession from "./FilterSession";
import SessionSelection from "./SessionSelection";


class App extends React.Component {
	render() {
		return (
			<Router basename='/'>
				<Switch>
					<Route path={process.env.PUBLIC_URL + "/"} exact component={MainPage}/>
					<Route path={process.env.PUBLIC_URL + "/session/:id"} component={FilterSession}/>
					<Route path={process.env.PUBLIC_URL + "/session/"} component={FilterSession}/>
					<Route path={process.env.PUBLIC_URL + "*"} component={SessionSelection} status={404} />
				</Switch>
			</Router>
		)
	}
}

export default App;
